// pages/login.js

import Image from 'next/image';
import { useRef } from "react";




export default function LoginDetails() {
    const fileInputRef = useRef(null);
  
    const handleUploadClick = () => {
      if (fileInputRef.current) {
        fileInputRef.current.click(); // programmatically click the hidden input
      }
    };

    return (
        <div className="login-page background-one">
            {/* Overlay */}
            <div className="overlay"></div>

            <div className="container">
                <div className="page-content">
                    <div className="login-sec">
                        <div className="col-md-6">
                            <div className="left">
                                <div className="logo">
                                    <Image
                                        src="/nexlearn-logo.webp"
                                        alt="NexLearn Logo"
                                        width={200}
                                        height={50}
                                    />
                                </div>
                                <div className="login-img">
                                    <Image
                                        src="/login-img.webp"
                                        alt="Login Image"
                                        width={335}
                                        height={260}
                                    />
                                </div>
                            </div>
                        </div>

                        <div className="col-md-6">
                            <div className="right">
                                <div className="login-form">
                                    <h2>Add Your Details</h2>

                                    <form action="">
                                        <div className="form-group upload-form-group">
                                            <div className="upload-content" onClick={handleUploadClick}>
                                                <img
                                                    src="/camera.svg"
                                                    alt="Upload Icon"
                                                    className="upload-icon"
                                                />
                                                <p>Add Your Profile Picture</p>
                                            </div>
                                            <input
                                                type="file"
                                                id="upload-photo"
                                                name="photo"
                                                accept="image/*"
                                                hidden
                                                ref={fileInputRef}
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="SmsCode">SMS Code</label>
                                            <div className="input-group">
                                                <input
                                                    type="number"
                                                    id="SmsCode"
                                                    name="SmsCode"
                                                    placeholder="Enter Code"
                                                    className="form-input"
                                                />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="qualification">Qualification</label>
                                            <select id="qualification" name="qualification" className="form-input">
                                                <option value="">Select Qualification</option>
                                                <option value="highschool">High School</option>
                                                <option value="bachelor">Bachelor's Degree</option>
                                                <option value="master">Master's Degree</option>
                                                <option value="phd">Ph.D.</option>
                                            </select>
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="district">District</label>
                                            <select id="district" name="district" className="form-input">
                                                <option value="">Select District</option>
                                                <option value="district1">District 1</option>
                                                <option value="district2">District 2</option>
                                                <option value="district3">District 3</option>
                                            </select>
                                        </div>

                                        <button type="submit" className="submit-btn">Get Started</button>

                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
